                    .then((res) => {return res.json()})
                    .then((data) => {
                        map.getSource('streets-selected').setData(data);                                   
                        // console.log(data.features[0].geometry); 
                        streetBufferArea = turf.buffer(data, 0.01)
                        //crashesInBuffer = turf.pointsWithinPolygon(crashPoints, streetBufferArea);
